<template>
    <div class="font-settings">
      <h2>Pengaturan Font</h2>
      
      <div class="font-options">
        <div 
          v-for="font in availableFonts" 
          :key="font.name"
          class="font-card"
          :style="{ fontFamily: font.name }"
          @click="changeFont(font)"
        >
          <span>{{ font.name }}</span>
          <div class="font-weights">
            <button
              v-for="weight in font.weights"
              :key="weight"
              @click.stop="changeFont(font, weight)"
              :style="{ fontWeight: weight }"
            >
              {{ weight }}
            </button>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script setup>
  import { useFontStore } from '@/stores/font'
  
  const fontStore = useFontStore()
  
  const availableFonts = [
    { name: 'Inter', weights: ['300', '400', '700'] },
    { name: 'Poppins', weights: ['300', '500', '700'] },
    { name: 'Roboto', weights: ['300', '400', '700'] },
    { name: 'Open Sans', weights: ['400', '600', '800'] },
    { name: 'Nunito Sans', weights: ['400', '600', '800'] },
    { name: 'San Francisco', weights:['400','700']}
  ]
  
  function changeFont(font, weight = '400') {
    fontStore.setFont(font.name, weight)
  }
  </script>
  
  <style scoped>
  .font-settings {
    padding: 1rem;
    max-width: 800px;
    margin: 0 auto;
  }
  
  .font-options {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 1rem;
    margin-top: 1.5rem;
  }
  
  .font-card {
    padding: 1rem;
    border: 1px solid #ddd;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .font-card:hover {
    background-color: #f5f5f5;
    transform: translateY(-3px);
  }
  
  .font-weights {
    display: flex;
    gap: 0.5rem;
    margin-top: 0.8rem;
  }
  
  .font-weights button {
    padding: 0.2rem 0.5rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    background: white;
    cursor: pointer;
  }
  </style>